<template>
  <v-card class="mx-auto rounded-lg">
    <v-card-title>
      <span class="text-h6 font-weight-bold">  </span>
      <v-spacer></v-spacer>
    </v-card-title>

    <v-progress-linear
      :indeterminate="isLoading"
      :active="isLoading"
      color="accent darken-2"
    ></v-progress-linear>

    <v-divider></v-divider>

    <v-card-text class="px-sm-12 py-2">
      <v-form v-model="valid" @submit.prevent="">
        <v-text-field
          v-model="title"
          label=""
          :rules="[required(''), isLongEnough('', 5)]"
        >
        </v-text-field>

        <v-textarea
          v-model="question"
          label="  ."
          rows="10"
          no-resize
          :rules="[required(''), isLongEnough('', 15)]"
          counter
        >
        </v-textarea>
      </v-form>
    </v-card-text>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="closeDialog">
        
      </v-btn>
      <v-btn
        color="blue darken-1"
        text
        :disabled="!valid || isLoading"
        @click="createNotice"
      >
        
        <v-icon right>
          mdi-checkbox-marked-circle
        </v-icon>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    isLoading: false,

    valid: false,

    title: "",
    question: "",

    required(propertyType) {
      return value => !!value || `${propertyType} .`;
    },
    isLongEnough(propertyType, limit) {
      return value =>
        value.length >= limit ||
        `${propertyType}  ${limit}  .`;
    }
  }),

  methods: {
    closeDialog() {
      //    Emit
      this.$emit("closeDialog");
    },
    createNotice() {
      this.isLoading = true;

      //      , FormData  .
      let formData = new FormData();

      formData.append("classId", this.$route.params.classId);
      formData.append("postName", this.title);
      formData.append("postDetail", this.question);

      this.$store.dispatch("post/createNotice", formData);

      setTimeout(() => {
        this.isLoading = false;

        this.closeDialog();
      }, 1000);
    }
  }
};
</script>

<style></style>
